#main_image {
  width: 200px;
  height: 200px;
  margin-top: 100px;
  opacity: 0.8;
  border: 7px solid rgb(210, 210, 210);
}

#main_text {
  margin-top: 40px;
  color: rgb(210, 210, 210);
  font-weight: bold;
}

#main_icons {
  margin-top: 15px;
  font-size: 35px;
}

#main_icons i {
  margin: 10px;
  color: rgba(255, 255, 255, 0.8) !important;
}

#main_icons i:hover {
  color: rgba(255, 255, 255, 1) !important;
}

#footer_text_main {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
  color: rgb(210, 210, 210);
}

#footer_text_mobile {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
  color: rgb(210, 210, 210);
}

#footer_text_snake {
  display: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
}

#counter_div {
  margin-top: 30px;
  display: none;
  font-family: press-start;
}

#name {
  font-size: 2.92rem;
  font-family: "Montserrat", sans-serif;
}

@media only screen and (max-width: 600px) {
  #name {
    font-size: 35px;
  }
  #main_image:hover {
    cursor: default;
  }
}

.icons {
  width: 32px;
  height: 32px;
}

.icons:hover {
  width: 40px;
  height: 40px;
}

